const button = document.querySelector(".button")
button.addEventListener("click", (e) => {
    console.log("yeap")
    const overlay = document.createElement("div")
    overlay.classList.add("overlay")
    document.body.appendChild(overlay)
    overlay.innerHTML = createModal(
        "Просто заполните форму заявки0",
        "и мы перезвоним вам в течении 10 минут", 
        "заказать кОНСУЛЬТАЦИЮ")
})


function createModal(subtitle, descr, submitText) {
    return `
    <div class="modal"> 
        <div class="modal__close">x</div>
        <div class="modal__subtitle">${subtitle}</div>
        <div class="modal__descr">${descr}</div>
        <form action="POST" class="feed-form">
            <input name="name" placeholder="Ваше имя" type="text" class="error" aria-invalid="true">
            <label id="name-error" class="error" for="name">Пожалуйста, введите свое имя</label> 
            <input name="phone" placeholder="Ваш телефон" class="error">
            <label id="phone-error" class="error" for="phone">Пожалуйста, введите свой номер телефона</label>
            <input name="email" placeholder="Ваш E-mail" type="email" class="error">
            <label id="email-error" class="error" for="email">Пожалуйста, введите свою почту</label> 
            <button class="button button_submit">${submitText}</button>
        </form>
    </div>
    
    `
}

